import React from 'react';
import { Link } from 'react-router-dom';
import { Table, Space } from 'antd';
import handelDataFetch from '../functions/fetch';

const DashboardTable = ({ data }) => {


    const handelDeleteData = async (id) => {
        try {
            const result = await handelDataFetch('/users', 'DELETE', JSON.stringify({_id: id}));

            if (result.status) {
                window.location.reload();
            } else {
                window.alert("Error in loading data!...");
            }
        } catch (error) {
            console.log(error);
        }
    }

    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            render: (_, record) => (
                <Space size="middle">
                    <Link to={`/user/profile/${record._id}`} className='link'> {record.name}</Link>
                </Space>
            )
        },
        {
            title: 'Age',
            dataIndex: 'age',
            key: 'age',
        },
        {
            title: 'Email',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: 'Mobile Number',
            dataIndex: 'mobileNumber',
            key: 'mobileNumber',
        },
        {
            title: 'Action',
            key: 'action',
            render: (_, record) => (
                <Space size="middle">
                    <Link to={`/user/edit/${record._id}`} className='btn btn-info'> Edit</Link>
                    <button className='btn btn-danger' onClick={() => handelDeleteData(record._id)}> Delete</button>
                </Space>
            ),
        },
    ];

    return <Table dataSource={data} columns={columns} />;
};

export default DashboardTable;
