import React from 'react'
import { Link } from 'react-router-dom'

const Profile = () => {
    return (
        <div className="container min-vh-100 p-3 p-md-4 p-lg-5">
            <div className='row d-flex justify-content-center align-item-center'>
                <div className="col-md-6 col-lg-4 col-xl-3">
                    <div className="card">
                        <div className="card-header text-dark fw-bold">
                            User Details:
                        </div>
                        <div className="card-body">
                            <ul className="list-group list-group-flush">
                                <li className="list-group-item">Name: </li>
                                <li className="list-group-item">Email: </li>
                                <li className="list-group-item">Mobile NO.: </li>
                                <li className="list-group-item">Age: </li>
                                <li className="list-group-item">Gender: </li>
                            </ul>
                        </div>

                    </div>
                </div>
            </div>
        </div>

    )
}

export default Profile