import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';

import handelDataFetch from '../functions/fetch';

const Form = () => {
    const { id } = useParams();
    const navigate = useNavigate();

    const [data, setData] = useState({
        name: "",
        email: "",
        mobileNumber: "",
        age: "",
        gender: ""
    });

    let name, value;

    const handelInputs = (e) => {
        name = e.target.name;
        value = e.target.value;

        setData({ ...data, [name]: value });
    }

    const handelSubmit = async (e) => {
        try {
            e.preventDefault();

            const result = await handelDataFetch('/users', (id)? "PATCH" : "POST", JSON.stringify(data));

            if (result.status) {
                navigate('/');
            } else {
                window.alert("Error while submitting data!...");
            }
        } catch (error) {
            console.log(error);
        }
    }

    const handelGetAllUser = async () => {
        try {
            const result = await handelDataFetch(`/filter/byUserId/${id}`, 'GET');

            if (result.status) {
                setData(result.result[0])
            } else {
                window.alert("Error in loading data!...");
            }
        } catch (error) {
            console.log(error);
        }
    }


    useEffect(() => {
        (id && handelGetAllUser());
    }, []);
    

    return (
        <div className='container min-vh-100 py-2 py-md-3 py-xl-4'>
            <div className="row d-flex justify-content-center align-items-center">
                <div className="col col-md-8 col-xl-6 border">
                    <form>
                        <div className="row py-3">
                            <h5 className="h5 d-flex justify-content-center align-items-center">Enter the form details</h5>
                        </div>
                        <div className="row">
                            <div className="mb-3 col-12">
                                <label htmlFor="name" className="form-label ms-1">Name : </label>
                                <input type="text" className="form-control" name='name' placeholder='Enter Name:' value={data.name} onChange={handelInputs}/>
                            </div>
                            <div className="mb-3 col-12">
                                <label htmlFor="email" className="form-label ms-1">Email : </label>
                                <input type="email" className="form-control" name='email' placeholder='Enter Email:' value={data.email} onChange={handelInputs}/>
                            </div>
                            <div className="mb-3 col-12 col-md-6 col-xl-8">
                                <label htmlFor="mobileNumber" className="form-label ms-1">Mobile : </label>
                                <input type="number" className="form-control" name='mobileNumber' placeholder='Enter Mobile:' value={data.mobileNumber} onChange={handelInputs}/>
                            </div>
                            <div className="mb-3 col-12 col-md-6 col-xl-4">
                                <label htmlFor="age" className="form-label ms-1">Age : </label>
                                <input type="number" className="form-control" name='age' placeholder='Enter Age:' value={data.age} onChange={handelInputs}/>
                            </div>
                            <div className="mb-3 col-12">
                                <label htmlFor="gender" className="form-label ms-1">Gender : </label><br />
                                <input type="radio" className="form-check-input ms-3 me-2" name='gender' value="male" checked={(id) && data.gender === "male"} id='male' placeholder='Enter Mobile:'  onChange={handelInputs}/> <label htmlFor="male" className="form-label">Male</label> <br />
                                <input type="radio" className="form-check-input ms-3 me-2" name='gender' value="female" checked={(id) && data.gender === "female"} id='female' placeholder='Enter Mobile:' onChange={handelInputs} /> <label htmlFor="female" className="form-label">Female</label> <br />
                                <input type="radio" className="form-check-input ms-3 me-2" name='gender' value="other" checked={(id) && data.gender === "other"} id='other' placeholder='Enter Mobile:' onChange={handelInputs}/> <label htmlFor="other" className="form-label">Other</label>
                            </div>
                            <div className="mb-3 d-flex justify-content-center align-items-center">
                                <button className='btn btn-success btn-lg' onClick={handelSubmit}><i className="fas fa-plus"></i> Submit</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Form