import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import handelDataFetch from '../functions/fetch';
import Dashboard from './Dashboard';
import { Dropdown } from 'antd';

function Main() {


    const [data, setData] = useState([]);

    const handelGetAllUser = async () => {
        try {
            const result = await handelDataFetch('/users', 'GET');

            if (result.status) {
                setData(result.result)
            } else {
                window.alert("Error in loading data!...");
            }
        } catch (error) {
            console.log(error);
        }
    }


    const handleGetValidUser = async () => {
        try {
            const result = await handelDataFetch('/filter/validAgeUser', 'GET');

            if (result.status) {
                setData(result.result)
            } else {
                window.alert("Error in loading data!...");
            }
        } catch (error) {
            console.log(error);
        }
    }


    useEffect(() => {
        handelGetAllUser();
    }, [])

    const items = [
        {
            key: '1',
            label: (
                <button className='btn' onClick={handelGetAllUser}>All Users</button>
            ),
        },
        {
            key: '2',
            label: (
                <button className='btn' onClick={handleGetValidUser}>Valid Users</button>
            ),
        },
    ];

    return (
        <div className="row p-md-3 p-xl-5 mt-0">
            <div className="row border m-0 px-2 py-4">
                <div className="row m-0 p-0">
                    <div className="col-12 d-flex justify-content-center align-items-center">
                        <Link to='user/add' className="btn btn-success mx-3"> <i className="fas fa-plus"></i> Add New User</Link>
                        <Dropdown menu={{items}} placement="bottomLeft" className='btn btn-secondary' arrow>
                            <button><i className="fas fa-filter"></i> Filter</button>
                        </Dropdown>
                    </div>
                </div>
                <div className="row p-2 g-2 g-md-1 g-lg-2">
                    <Dashboard data={data} />
                </div>
            </div>
        </div>
    )
}

export default Main